* {
  box-sizing: border-box;
}

body {
  margin: 0;
  max-height: 100vh;
}

span {
  margin: 0;
  padding: 0;
}

main {
  background: #23252C;
  min-height: 100vh;
  color: #FFFFFFFF;
  font-family: Inter;

  display: flex;
  justify-content: center;
  align-items: center;
  
  padding-top: 44px;
  padding-bottom: 44px;

  flex-basis: auto;

}


.card{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  
  background-color: #1A1B21;
  border-radius: 10px 10px 10px 10px;
}

/*

INFORMATION

*/

.info--container {
  border-radius: 10px 10px 0px 0px;
}

.info {
  margin: 0;
}

.name {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 30px;

  padding-top: 20px;
  padding-left: 70px;
  padding-right: 70px;
  padding-bottom: 0;

  margin: 0;
}

.title {
  font-style: normal;
  font-weight: 400;
  font-size: 12.8px;
  line-height: 15px;

  color: #F3BF99;

  padding: 0px 70px 9px 70px;
  margin: 0;
}

.email {
  font-style: normal;
  font-weight: 400;
  font-size: 10.24px;

  color: #F5F5F5;

  margin: 0;
  padding: 0px 45px 0px 45px;
}

.image {
  border-radius: 10px 10px 0px 0px;
  padding: 0;
}

.buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;

  padding-top: 15px;
  padding-bottom: 33px;
}

.email--button {
  display: flex;
  flex: 0.35; /* to make buttons same size*/
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 9px 13px 9px 11px;

  background: #FFFFFF;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;

  color: #374151;

  border: 1px solid #D1D5DB;
  box-sizing: border-box;

box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
border-radius: 6px;
}

.btn--icon {

  flex: none;
  order: 0;
  flex-grow: 0;
  margin: 0px 8px;
}

.linkedin--button {

  flex: 0.35; /* to make buttons same size*/
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 9px 13px 9px 11px;

  background: #5093E2;

  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;

  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;

  color: #FFFFFF;


}

h2 {
  margin: 0;
}


